import Vue from 'vue'
import Router from 'vue-router'
import login from '@/views/login'
import AppMain from '@/views/AppMain'
import shangpin from '@/views/shangpin'
import sptype from '@/views/sptype'
import zidian from '@/views/zidian'

Vue.use(Router)
const VueRouterPush = Router.prototype.push
Router.prototype.push = function push (to) {
    return VueRouterPush.call(this, to).catch(err => err)
}
export default new Router({
  // mode:'history',
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/AppMain',
      name: 'AppMain',
      component: AppMain,
      children:[
        {
          path: '/',
          name: 'shangpin',
          component: shangpin,
        },
        {
          path: '/shangpin',
          name: 'shangpin',
          component: shangpin,
        },
		{
		  path: '/sptype',
		  name: 'sptype',
		  component: sptype,
		},
        {
          path: '/zidian',
          name: 'zidian',
          component: zidian,
        }
      ]
    }
  ]
})



