export default {

    setAsideClassByAsync: function(context, payload) {
      if(payload.collapsed == false){
        context.commit('setAsideClass', payload);//Action提交的是mutation
      }else{
        setTimeout(() => {
        	context.commit('setAsideClass', payload);//Action提交的是mutation
        }, 500);
      }

    }
	}



