/**
 * 对后台请求的地址的封装，URL格式如下：
 * 模块名_实体名_操作
 */
export default {
  'SERVER': 'http://localhost:7201', //服务器

  'USER_LOGIN':'/user/login',//登录
  'USER_REG':'/user/reg',//注册

  'SHANGPING_LIST':'/shangPing/shangPinByTypeOrName',//查询商品
  'SHANGPING_DEL':'/shangPing/delshangPin',//删除商品
  'SHANGPING_MAGER':'/shangPing/addOrUpdateShangpin',//新增修改商品



  'TAB_LIST':'/Tab/listTabAll',//查询商品类别
  'TAB_DEL':'/Tab/delTab',//删除商品类别
  'TAB_MAGER':'/Tab/addOrUpdateTab',//新增修改商品类别



  'ZIDIAN_LIST':'/zidian/listZidian',//查询字典
  'ZIDIAN_BY':'/zidian/getZidianBytype',//按条件查询字典
  'ZIDIAN_DEL':'/zidian/delZidian',//删除字典
  'ZIDIAN_MAGER':'/zidian/addOrUpdateZidian',//新增修改字典

	'getFullPath': k => { //获得请求的完整地址，用于mockjs测试时使用
		return this.SERVER + this[k];
	}
}



