<template>
  <div class="hello">
    <el-tag
      key="tag.name"
      type="tag.type">
      数据字典
    </el-tag>
    <hr />
    <el-form :model="searchForm" :inline="true">
      <el-form-item label="字典名称:">
        <el-input v-model="searchForm.sNo" placeholder="请输入字典名称"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="search" >查询</el-button>
        <el-button type="info" @click="mager('add')" >新增</el-button>
      </el-form-item>

    </el-form>
    <el-table
        :data="tableData"
        style="width: 100%">
        <el-table-column
          type="index"
          label="序号"
          width="180">
        </el-table-column>

        <el-table-column
          prop="zdType"
          label="类型"
          width="180">
        </el-table-column>
        <el-table-column
          prop="zdName"
          label="名称"
          width="180">
        </el-table-column>

        <el-table-column label="操作">
         <template slot-scope="scope">
            <el-button @click="del(scope.row.zdId)" type="text" size="small" style="color: red;">删除</el-button>
            <el-button @click="mager(scope.row)" type="text" size="small" style="color: orange;">修改</el-button>
         </template>
        </el-table-column>
      </el-table>
      <br />
      <el-pagination
        background
        :page-size="rows"
        :page-sizes="[5, 10, 15, 20]"
        layout="sizes,total,prev, pager, next "
        :total="total" @size-change="handleSizeChange" :current-page="page" @current-change="handleCurrentChange">
      </el-pagination>



      <el-dialog
        :title="title"
        :visible.sync="centerDialogVisible"
        width="30%"
        center>
        <el-form  :model="zidianForm" ref="zidianForm" :rules="rules">
          <input  type="hidden" v-model="zidianForm.zdId"></el-input>
          <el-form-item prop="zdType" label="类型" style="margin-bottom: 10px;">
            <el-input placeholder="请输入类型" v-model="zidianForm.zdType"></el-input>
          </el-form-item>
          <el-form-item prop="zdName" label="名称" style="margin-bottom: 10px;">
            <el-input placeholder="请输入名称" v-model="zidianForm.zdName"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="centerDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="saveOrUpdate">确 定</el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
  var count = 0;
export default {
  name: 'HelloWorld',
  data () {
    return {
      title:'新增',
      searchForm:{
        sName:'',
        sSex:''
      },
      zidianForm:{
        zdType:'',
        zdName:'',
      },
       centerDialogVisible:false,
       tableData: [],
       total:0,
       page:1,
       rows:5,
      rules: {
                zdType: [
                  { required: true, message: '请输入姓名', trigger: 'blur' }
                ],
                zdName: [
                  { required: true, message: '请输入类型', trigger: 'blur' }
                ],
      }
    }

  },
  methods:{
    saveOrUpdate(){
      this.$refs['zidianForm'].validate((valid) => {
        if(valid){
          let url = this.axios.urls.ZIDIAN_MAGER;
            this.axios.post(url,this.zidianForm).then(resp=>{
                this.$message({
                          message: resp.data.msg,
                          type: 'success'
                        });
                this.centerDialogVisible = false;
                this.search();
            }).catch(resp=>{

            });
        }else{
          return false;
        }
      });
    },
    mager(s){
      if(count>0){
        this.$refs['zidianForm'].resetFields();
      }
      if(s == 'add'){
        this.title='新增字典';
        this.zidianForm = {
          zdId:0,
        };
      }else{
        this.title='修改字典';
        this.zidianForm={
          zdId:s.zdId,
          zdType:s.zdType,
          zdName:s.zdName
        }
      }
      this.centerDialogVisible = true;

      count++;
    },
    del(id){
      this.$confirm('此操作将永久删除该商品, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              }).then(() => {
                let url = this.axios.urls.ZIDIAN_DEL;
                let form = {
                  zdId:id
                }
                this.axios.post(url,form).then(resp=>{
                  this.$message({
                            message: resp.data.msg,
                            type: 'success'
                          });
                  this.search();
                }).catch(resp=>{

                });
              }).catch(() => {
                this.$message({
                  type: 'info',
                  message: '已取消删除'
                });
      });


    },
    search(){
      let url = this.axios.urls.ZIDIAN_LIST;
      let form = {
        current:this.page,
        rows:this.rows
      }
      this.axios.post(url,form).then(resp=>{
          this.tableData = resp.data.result;
          this.total = resp.data.total;
      }).catch(resp=>{

      });

    },
    handleSizeChange(rows){
      this.rows = rows;
      this.search();
    },
    handleCurrentChange: function(page) {
      this.page=page;
      this.search();
    },
  },
  created(){
    this.search();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



