<template>
  <el-menu router :default-active="$route.path" class="el-menu-vertical-demo" background-color="#334157"
  text-color="#fff" active-text-color="#ffd04b" :collapse="collapsed" :collapse-transition="bb" :default-openeds="treeNodeList" :unique-opened="true">
    <div class="logobox">
      <img class="logoimg" src="../assets/img/logo.png" alt="">
    </div>
   <el-submenu index="1">
      <template slot="title">
        <i class="el-icon-user-solid"></i>
        <span>商品管理</span>
      </template>
      <el-menu-item index="/shangpin">
        <template slot="title">
          <!-- <i :class="n2.icon"></i> -->
          <span>商品管理</span>
        </template>
      </el-menu-item>
      <el-menu-item index="/sptype">
        <template slot="title">
          <!-- <i :class="n2.icon"></i> -->
          <span>类别管理</span>
        </template>
      </el-menu-item>

    </el-submenu>
    <el-submenu index="2">
       <template slot="title">
         <i class="el-icon-document"></i>
         <span>订单管理</span>
       </template>
       <el-menu-item index="/zidian">
         <template slot="title">
           <!-- <i :class="n2.icon"></i> -->
           <span>订单管理</span>
         </template>
       </el-menu-item>
    </el-submenu>
    <el-submenu index="3">
       <template slot="title">
         <i class="el-icon-s-home"></i>
         <span>地址管理</span>
       </template>
       <el-menu-item index="/zidian">
         <template slot="title">
           <!-- <i :class="n2.icon"></i> -->
           <span>地址管理</span>
         </template>
       </el-menu-item>

     </el-submenu>

     <el-submenu index="4">
       <template slot="title">
         <i class="el-icon-s-custom"></i>
         <span>用户管理</span>
       </template>
       <el-menu-item index="/zidian">
         <template slot="title">
           <!-- <i :class="n2.icon"></i> -->
           <span>用户管理</span>
         </template>
       </el-menu-item>

     </el-submenu>


  </el-menu>
</template>
<script>
  export default {
    data: function() {
      return {
        bb: true, //过渡动画
        treeNodeList: ["1"]
      }
    },


    computed: {
      collapsed: function() {
        return this.$store.getters.getcollapsed;
      }
    }
  }
</script>
<style>
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 240px;
    min-height: 400px;
  }

  .el-menu-vertical-demo:not(.el-menu--collapse) {
    border: none;
    text-align: left;
  }

  .el-menu-item-group__title {
    padding: 0px;
  }

  .el-menu-bg {
    background-color: #1f2d3d !important;
  }

  .el-menu {
    border: none;
  }

  .logobox {
    height: 40px;
    line-height: 40px;
    color: #9d9d9d;
    font-size: 20px;
    text-align: center;
    padding: 20px 0px;
  }

  .logoimg {
    height: 40px;
  }
</style>



