<template>
  <div class="hello">
    <el-tag
      key="tag.name"
      type="tag.type">
      商品管理
    </el-tag>
    <hr />
    <el-form :model="searchForm" :inline="true">
      <el-form-item label="商品名称:">
        <el-input v-model="searchForm.sNo" placeholder="请输入商品名称"></el-input>
      </el-form-item>
      <el-form-item label="商品类别:">
       <el-select v-model="searchForm.sType" placeholder="请选择类型" style="width: 100%;">
             <el-option v-for="item in sTypes"
               :key="item.tid"
             :label="item.tname"
             :value="item.tid">

             </el-option>
           </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="search" >查询</el-button>
        <el-button type="info" @click="mager('add')" >新增</el-button>
      </el-form-item>

    </el-form>
    <el-table
        :data="tableData"
        style="width: 100%">
         <el-table-column prop="sImg" label="图片" align="center" width="150px" padding="0px"
            :show-overflow-tooltip = "true">
                 <template slot-scope="scope">
                      <img :src="imgpath+scope.row.sImg" alt="" style="width: 36px;height:36px">
                 </template>
              </el-table-column>

        <el-table-column
          prop="sName"
          label="名称"
          width="180">
        </el-table-column>
        <el-table-column
          prop="sType"
          label="类型"
          :formatter="getType"
          width="180">
        </el-table-column>
        <el-table-column
          prop="sRemark"
          label="描述">
        </el-table-column>
        <el-table-column
          prop="sMoney"
          label="价格">
        </el-table-column>
        <el-table-column label="操作">
         <template slot-scope="scope">
            <el-button @click="del(scope.row.shId)" type="text" size="small" style="color: red;">删除</el-button>
            <el-button @click="mager(scope.row)" type="text" size="small" style="color: orange;">修改</el-button>
         </template>
        </el-table-column>
      </el-table>
      <br />
      <el-pagination
        background
        :page-size="rows"
        :page-sizes="[5, 10, 15, 20]"
        layout="sizes,total,prev, pager, next "
        :total="total" @size-change="handleSizeChange" :current-page="page" @current-change="handleCurrentChange">
      </el-pagination>



      <el-dialog
        :title="title"
        :visible.sync="centerDialogVisible"
        width="40%"
        center>
            <el-form  :model="shangpinForm" ref="shangpinForm" :rules="rules">
            <el-container>
              <el-aside width="240px" style="margin-top: 50px;text-align: center;">
                <el-form-item prop="sImg">
                  <img id="img"  :src="imgpath+shangpinForm.sImg" width="240px" height="240px"/>
                </el-form-item>
                <el-button plain @click="getImg">请选择图片</el-button>
                <input type="file" hidden="hidden" id="file"></input>
              </el-aside>
              <el-main width="400px">
                <input  type="hidden" v-model="shangpinForm.shId"></el-input>
                <el-form-item prop="sName" label="名称" style="margin-bottom: 10px;">
                  <el-input placeholder="请输入名称" v-model="shangpinForm.sName"></el-input>
                </el-form-item>
                <el-form-item prop="sType" label="类型" style="margin-bottom: 10px;">
                  <el-select v-model="shangpinForm.sType" placeholder="请选择类型" style="width: 100%;">
                        <el-option v-for="item in sTypes"
                          :key="item.tid"
                        :label="item.tname"
                        :value="item.tid">

                        </el-option>
                      </el-select>
                </el-form-item>
                <el-form-item prop="sRemark" label="描述" style="margin-bottom: 10px;">
                   <el-input placeholder="请输入商品描述" v-model="shangpinForm.sRemark"></el-input>
                </el-form-item>
                <el-form-item prop="sMoney" label="价格" style="margin-bottom: 0;">
                  <el-input placeholder="请输入价格" v-model="shangpinForm.sMoney"></el-input>
                </el-form-item>
              </el-main>
            </el-container>
            </el-form>





            <span slot="footer" class="dialog-footer">
              <el-button @click="centerDialogVisible = false">取 消</el-button>
              <el-button type="primary" @click="saveOrUpdate">确 定</el-button>
            </span>
      </el-dialog>
  </div>
</template>

<script>
  var count = 0;
export default {
  name: 'HelloWorld',
  data () {
    return {
      title:'新增商品',
      imgpath:'http://192.168.137.209:7201/images/',
      searchForm:{
        sNo:'',
        sName:'',
        sSex:''
      },
      shangpinForm:{
        shId:'',
        sName:'',
        sType:'',
        sRemark:'',
        sMoney:'',
        sImg:'common/zw.jpg'
      },
       centerDialogVisible:false,
       tableData: [],
       sTypes:[],
       total:0,
       page:1,
       rows:5,
      rules: {
                sName: [
                  { required: true, message: '请输入姓名', trigger: 'blur' }
                ],
                sType: [
                  { required: true, message: '请输入类型', trigger: 'blur' }
                ],
                sRemark: [
                  { required: true, message: '请输入描述', trigger: 'blur' }
                ],
                sMoney: [
                  { required: true, message: '请输入价格', trigger: 'blur' }
                ],
      }
    }

  },
  methods:{
    getType(a,b){
      for(var i = 0;i<this.sTypes.length;i++){
        if(a.sType == this.sTypes[i].tid){
          return this.sTypes[i].tname;
        }
      }
    },
    getImg(){
      var fileInput = document.getElementById("file");
      fileInput.click();
      fileInput.addEventListener('change', function() {
        var file = fileInput.files[0];
        if (window.FileReader)
                {
                    var reader = new FileReader();
                    reader.readAsDataURL(file);
                    //监听文件读取结束后事件    
                    reader.onloadend = function (e) {
                       document.getElementById("img").src=e.target.result;
                    };
                }
      })


    },
    saveOrUpdate(){
      this.$refs['shangpinForm'].validate((valid) => {
        if(valid){
          let url = this.axios.urls.SHANGPING_MAGER;
            this.axios.post(url,this.shangpinForm).then(resp=>{
                this.$message({
                          message: resp.data.msg,
                          type: 'success'
                        });
                this.centerDialogVisible = false;
                this.search();
            }).catch(resp=>{

            });
        }else{
          return false;
        }
      });
    },
    mager(s){

      if(count>0){
        this.$refs['shangpinForm'].resetFields();
      }
      if(s == 'add'){
        this.title='新增商品';
        this.shangpinForm = {
          shId:0,
           sImg:'common/zw.jpg'
        };
      }else{
        this.title='修改商品';
        this.shangpinForm={
          shId:s.shId,
          sName:s.sName,
          sType:s.sType,
          sRemark:s.sRemark,
          sMoney:s.sMoney,
          sImg:s.sImg
        }
      }
      this.centerDialogVisible = true;

      count++;
    },
    del(id){
      this.$confirm('此操作将永久删除该商品, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              }).then(() => {
                let url = this.axios.urls.SHANGPING_DEL;
                let form = {
                  shId:id
                }
                this.axios.post(url,form).then(resp=>{
                  this.$message({
                            message: resp.data.msg,
                            type: 'success'
                          });
                  this.search();
                }).catch(resp=>{

                });
              }).catch(() => {
                this.$message({
                  type: 'info',
                  message: '已取消删除'
                });
      });


    },
    search(){
      let url = this.axios.urls.SHANGPING_LIST;
      let form = {
        sType:0,
        sName:'',
        current:this.page,
        rows:this.rows
      }
      this.axios.post(url,form).then(resp=>{
          this.tableData = resp.data.result;
          this.total = resp.data.total;
      }).catch(resp=>{

      });

    },
    refresh(){
      this.searchForm={
        sNo:'',
        sName:'',
        sSex:''
      };
      this.page=1;
      this.search();
    },
    handleSizeChange(rows){
      this.rows = rows;
      this.search();
    },
    handleCurrentChange: function(page) {
      this.page=page;
      this.search();
    },
  },
  created(){
    this.search();
    let url = this.axios.urls.TAB_LIST;
    let form = {
      current:this.page,
      rows:50
    }
    this.axios.post(url,form).then(resp=>{
        this.sTypes = resp.data.result;
    }).catch(resp=>{

    });
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



