<template>
  <div>
    <img src="../assets/bgImg.jpg" class="bgImg" />
    <div class="content">
    	<div class="bidTitle">{{title}}</div>
    	<div class="logCon">
        <el-form :model="userForm" ref="userForm" :rules="rules">
          <span>账号:</span>
          <el-form-item class="line" prop="uName">
              <el-input class="bt_input" type="text" v-model="userForm.uName" placeholder="请输入用户名" />
          </el-form-item>
          <div></div>
          <span>密码:</span>
          <el-form-item class="line" prop="uPassword">
              <el-input class="bt_input" type="password" v-model="userForm.uPassword" placeholder="请输入密码" />
          </el-form-item>
          <el-form-item class="line">
            <el-button type="primary" class="logingBut" v-if="reglogin=='login'" @click="login('userForm')">{{btn}}</el-button>
            <el-button type="primary" class="logingBut" v-if="reglogin=='reg'" @click="reg('userForm')">{{btn}}</el-button>
          </el-form-item>
        </el-form>
        <el-link class="regOrlogin" type="primary" @click="regOrlogin(reglogin)" >{{toText}}</el-link>
    	</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      reglogin:'login',
      title:'登录界面',
      toText:'去注册',
      btn:'登录',
      userForm:{
        uName:'',
        uPassword:''
      },
       rules: {
                uName: [
                  { required: true, message: '请输入账号', trigger: 'blur' },
                  { min: 3, max: 15, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                ],
                uPassword: [
                  { required: true, message: '请输入密码', trigger: 'blur' }
                ],
      }
    }
  },
  methods:{
    login:function(formName){
      let url = this.axios.urls.USER_LOGIN;
      this.$refs[formName].validate((valid) => {
          if (valid) {
             this.$router.push("/AppMain")
            // this.axios.post(url, this.userForm).then(resp => {
            //   if(resp.data.code==200){
            //     this.$router.push("/AppMain")
            //     this.$message({
            //               message: resp.data.msg,
            //               type: 'success'
            //             });
            //   }else if(resp.data.code==500){
            //     this.$message.error(resp.data.msg);
            //   }
            // }).catch(resp => {

            // });
          } else {
            return false;
          }
      });


    },
    reg(formName){
      let url = this.axios.urls.USER_REG;
      this.$refs[formName].validate((valid) => {
          if (valid) {
            this.axios.post(url, this.userForm).then(resp => {
              if(resp.data.code==200){
                this.regOrlogin('reg','ok');
                this.$message({
                          message: resp.data.msg,
                          type: 'success'
                        });
              }else if(resp.data.code==201){
                this.$message({
                          message: resp.data.msg,
                          type: 'warning'
                        });
              }else if(resp.data.code==500){
                this.$message.error(resp.data.msg);
              }
            }).catch(resp => {

            });
          } else {
            return false;
          }
      });
    },
    regOrlogin(str,state){
      if(str == 'login'){
          this.reglogin = 'reg';
          this.title='注册界面';
          this.toText='去登录';
          this.btn='注册';
      }else if(str == 'reg'){
          this.reglogin = 'login';
          this.title='登录界面';
          this.toText='去注册';
          this.btn='登录';
      }
      if(state != 'ok'){
        this.$refs['userForm'].resetFields();
      }
    }
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>



 .bgImg {
  	position: absolute;
  	top: 0px;
  	left: 0px;
  	bottom: 0px;
  	right: 0px;
  	width: 100%;
  	height: 100%;
  }

  .content {
    text-align: center;
  	width: 42%;
  	position: fixed;
  	background: #0f1a1494;
  	padding: 5% 1%;
    left: 27%;
  	margin-top: 10%;
  	border-radius: 5px;
  }
  .bidTitle {
  	color: #e6e6e6;
  	font-size: 34px;
  	font-weight: 600;
  }

  .logCon {
  	color: #FFFFFF;
  	margin-top: 40px;
  }

  .line {
    display: inline-block;
    margin-left: 5px;
    width: 50%;
  }


  .logingBut {
  	background: #2d8c5fa6;
  	border: none;
  	padding: 12px 0px;
  	color: #edefee;
  	border-radius: 2px;
  	width: 100%;
  	margin-left: 20px;
  	margin-top: 5px;
  }

  .bt_input {
  	border-radius: 2px;
  	border: none;
  	padding: 5px 0px;
  	width: 100%;
  	margin-right: 20px;
  	color: #333;
  }

  .logCon span {
  	font-size: 18px;
  	height: 40px;
  	line-height: 40px;
  }
  .regOrlogin{
      position: absolute;
      right: 10px;
      bottom: 10px;
   }
</style>



